.ColumnListItem:last-child {
  margin-bottom: 0;
}

.ColumnListItem {
  margin-bottom: 5px;
}

:first-child > .item-row {
  margin-top: -1px;
}

.ColumnListItem .item-row {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  border-radius: 2px;
  align-items: center;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #c3c7cb;
}

.ColumnListItem .item-row__labels {
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
  -moz-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.ColumnListItem .item-row__name {
  color: #4b637c;
  font-size: 15px;
  display: flex;
}

.ColumnListItem .help-tooltip {
  width: 100%;
}

.ColumnListItem .profile-content {
  display: block;
}

.ColumnListItem .profile-attributes {
  margin-bottom: 0;
}

.ColumnListItem .ModelSummary .label-value-pairs__label,
.ColumnListItem .ModelSummary .label-value-pairs__value {
  color: #fff;
}
