.SearchBar {
  display: flex;
}

.SearchBar.no-results {
  margin: 100px auto;
  padding: 0;
}

.SearchBar.no-results,
.SearchBar.no-results .search,
.SearchBar.no-results .input-container {
  width: 100%;
  max-width: 590px;
}

.SearchBar.with-results .search {
  margin: 0;
  width: 100%;
}

.SearchBar.with-results,
.SearchBar.with-results .search,
.SearchBar.with-results .input-container {
  width: 100%;
  max-width: 590px;
}

.SearchBar.constant-width,
.SearchBar.constant-width .search,
.SearchBar.constant-width .input-container {
  width: 100%;
}

.SearchBar .SearchBox {
  flex: 1;
}
