.DualColumnSearch {
  position: relative;
  margin: 0 0 25px;
}

.DualColumns {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.Column {
  box-sizing: border-box;
  width: calc(50% - 15px);
}

.column-header {
  margin-bottom: 15px;
}

.column-title {
  color: #3d454d;
  text-transform: uppercase;
  margin-right: 10px;
}

.DualColumnSearch .column-message {
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 5px;
}
